.app-wrapper {
  margin: 0 10%;
  display: grid;
  grid-template-areas:
  "header header"
  "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;

  font-family: "Yu Gothic UI", serif;
  font-size: 18px;
}
header {
  grid-area: header;
}
nav {
  grid-area: nav;
}
.app-wrapper-content {
  grid-area: content;
}

a {
  text-decoration: none;
}

.avatar-wrapper img {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  overflow: hidden;
}

textarea {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid lightblue;
  font-size: 18px;
}